@import "libs/normalize";
@import "components/header";
@import "components/search";
@import "components/sort";
@import "components/categories";
@import "components/card";
@import "components/book";

body {
  background-color: #d0d7de;
}

.wrapper {
  width: calc(100vw - 100px);
  background-color: #fff;
  margin: 50px auto;
  border-radius: 10px;
  max-width: 1400px;
  overflow: hidden;
  @media (max-width: 540px) {
    width: 100%;
    min-height: 100vh;
    margin: 0;
  }
}

.home {
  text-align: center;
  padding: 50px;

  &__image {
    padding-bottom: 20px;
  }

  @media (max-width: 420px) {
    padding: 20px;
  }
}

.main {
  padding: 20px;

  &__results {
    text-align: center;
    margin-bottom: 20px;
    font-weight: 500;
  }

  &__cards {
    display: flex;
    flex-wrap: wrap;
    column-gap: 30px;
    row-gap: 20px;
    align-items: center;
    justify-content: center;
  }
}

.no-results {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 30px 0;

  &--title {
    font-weight: 800;
    font-size: 32px;
  }
}

.not-found {
  padding: 50px 0;
  text-align: center;
  font-size: 20px;

  h2 {
    padding-bottom: 20px;
  }

  a {
    color: #1e90ff;
    text-decoration: underline;
  }
}

.popup {
  position: absolute;
  right: 0;
  margin-top: 15px;
  background: #ffffff;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.09);
  border-radius: 10px;
  overflow: hidden;
  padding: 10px 0;
  width: 120px;

  ul {
    overflow: hidden;

    li {
      color: black;
      padding: 12px 20px;
      cursor: pointer;

      &.active,
      &:hover {
        background: rgba(70, 63, 61, 0.15);
      }

      &.active {
        font-weight: bold;
      }
    }
  }
}

.button {
  background: #313131;
  color: #ffffff;
  font-size: 26px;
  font-weight: 600;
  padding: 10px;
  border-radius: 10px;
  cursor: pointer;
  display: block;
  margin: 0 auto;

  &:hover {
    background: lighten(#313131, 6.25%);
  }

  &:active {
    background: lighten(#313131, 12.25%);
  }

}

.spinner {
  display: block;
  margin: 0 auto;
}
